.casino__container{
    background: linear-gradient( #275225,black) ;
    max-width: 205px;
    font-size: 10px;    
    color: white;
    border-radius: 5px;
}
.casino__container h3{
    text-align: center;
    margin: 0;
    padding:10px 0;
    color: gold;
}
.casino__container-value{
    padding:10px;
}
.casino__container p{
    margin: 0;
    padding:5px 0;
}
.casino__container-value-items-price{
    display: flex;
    justify-content: space-around;
    
}
.casino__container-value-price{
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}
.casino__container-value-price-img{
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    align-items: center;
}
.casino__container-value-price-img img{
height: 15px;
width:15px;
}
.casino__container-value-price-bigitem{
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    align-items: center;
}

.casino__container-value-price-bigitem img{
    width: 25px;
    height: 25px;
    
}
.border-top{
    border-top:1px solid white;
}