
.exchanger__container{
    max-width: 580px;
    background:radial-gradient(#4c110c, #100200);
    text-align: center;
    color: white;
    justify-content: center;
    font-size: 10px;
    border:5px solid #4B0E01;
    border-radius: 10px;
}
.exchanger__container p{
    padding: 20px;
}
.exchanger__container-buttom{
    padding: 25px;
    
}
.exchanger__container-buttom buttom{
    margin: auto;
    display: flex;
    font-size: 10px;
    font-weight: 600;
    height: 30px;
    width: 235px;
    color: #783C10;
    background-color: gold;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
}

.exchanger__container-buttom buttom:hover{
    background-color: #54D101;
    color:#268514;
}
